@value colors: "../../../utils/colors.css";
@value primary, secondary from colors;

.SearchBar {
	position: relative;
	margin: auto auto;
	max-width: 500px;
	width: 100%;
}
.SearchBar input {
	width: 100%;
	background: #FFF;
	border: 1px solid #e6e6e6;
	border-radius: 20px;
	height: 40px;				/* Changes in Skinny bar */
	font-size: 20px;			/* Changes in Skinny bar */
	font-weight: 300;
	color: #353535;
	-webkit-box-shadow: 4px 2px 2px rgba(0,0,0,.04);
	        box-shadow: 4px 2px 2px rgba(0,0,0,.04);
	padding: 10px 20px;
	margin:0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.SearchBar input:focus {
	outline: none;
	border: 1px solid primary;
	background: #FFF;
}
.SearchBar input::-webkit-input-placeholder {
	color: #aaa;
}
.SearchBar input:-ms-input-placeholder {
	color: #aaa;
}
.SearchBar input::-ms-input-placeholder {
	color: #aaa;
}
.SearchBar input::placeholder {
	color: #aaa;
}


.Skinny input {
	height: 30px;				/* Changes in Skinny bar */
	font-size: 14px;			/* Changes in Skinny bar */
}
.Nav input {
	-webkit-box-shadow: 4px 2px 2px rgba(0,0,0,.04),
		inset 2px 2px 4px rgba(0,0,0,.25);
	        box-shadow: 4px 2px 2px rgba(0,0,0,.04),
		inset 2px 2px 4px rgba(0,0,0,.25);
	border: 0px;
}
.Nav input:focus {
	border: none;
}
/* Search Bar Button Styles */
.SearchBar button {
	position: absolute;
	right: 0;
	height: 100%;
	cursor: pointer;
	background: transparent;
	border: none;

}
.SearchBar button:focus {
	outline: none;
}
.SearchBar button:active {
	background: transparent;
}
.SearchBar button span {
	font-size: 20px;
	vertical-align: middle;
	color: #999;
}
.SearchBar i {
	color: #FF4040;
	background: -webkit-gradient(linear, left top, right top, from(transparent) , to(rgba(255,255,255,1)));
	background: -webkit-linear-gradient(left, transparent , rgba(255,255,255,1));
	background: -o-linear-gradient(left, transparent , rgba(255,255,255,1));
	background: linear-gradient(90deg, transparent , rgba(255,255,255,1));
	text-shadow: 0px 0px 5px rgba(255, 64, 64, .75);
}


/* The Search Bar on the search page should only be display when the width of the page is small 
	enough that the search bar in the navbar is not shown */
@media (min-width: 600px) {
	.SearchPage {
		display: none;
	}
}


