@value colors: "../../utils/colors.css";
@value lightGrey from colors;

.Connection {
}
.TracksContainer {
	background: -webkit-linear-gradient(45deg, #FFEFBD, #FFD9F2);
	background: -o-linear-gradient(45deg, #FFEFBD, #FFD9F2);
	background: linear-gradient(45deg, #FFEFBD, #FFD9F2);
	background: #F3F3F3;
	background: #FFF;
	width: 100%;
	padding: 20px 20px;
	border-top: 1px solid #E7E7E7;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
/* Holds Types and SubmittedBy Info */
.MetaContainer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	border-bottom: 1px solid lightGrey;
}
.SubmittedBy {
	color: #aaa;
	font-size: 14px;
}


