@value colors: "../../../utils/colors.css";
@value primary, secondary from colors;

.Track {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.TrackContainer {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row;
	    flex-flow: row;
}

.AlbumCover {
	width: 80px;
	height: 80px;
}
.SmallAlbumCover {
	width: 50px;
	height: 50px;
}
.AlbumCover > img {
	width: 100%;
	height: 100%;
}
.AlbumOutline {
	border: 2px solid primary;
}


.TextInfo {
	color: #444;
	font-size: 14px;
	font-weight: 400;
	padding: 0 10px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: auto;
}
.TextInfo > div {
	width: 100%;
}
.Title {
	color: #111;
	font-size: 16px;
}
.Artist {
	
}
.Album {
	color: #888;
}
.Year {
	font-size: 12px;
	text-align: center;
}


