.SubCategoryLink {
	width: 120px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	margin: 20px 10px;

}

.LinkImage {
	position: relative;
	width: 100px;
	height: 100px;
	margin:auto;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.LinkImage:after {
	content: '';
	display: block;
	position: absolute;
	top: 10px;
	right: 10px;
	bottom: 10px;
	left: 10px;
	background-color: rgba(250,250,250,1);
	-webkit-transition: .3s all;
	-o-transition: .3s all;
	transition: .3s all;
}

.LinkImage:hover:after {
	background-color: rgba(0,0,0,1);
}
.LinkImage:hover .LinkCaption {
	color: #FFF;
}
.SubCategoryLink img{
	width: 100px;
	height: 100px;
	display: block;
	padding: 0px;
	margin:  0px;
	-webkit-box-shadow: 0 4px 4px rgba(0,0,0,.25);
	        box-shadow: 0 4px 4px rgba(0,0,0,.25);
	-webkit-filter: hue-rotate(0deg) invert(5%);
	        filter: hue-rotate(0deg) invert(5%);
}
.LinkCaption {
	position: absolute;
	border: 1px solid;
	margin: 0;
	padding: 2px;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	max-width: 70px;
	z-index: 900;
	color: #000;
}

.SubCategoryLink a {
	text-decoration: none;
}

.Caption {
	color: #030017;
	width: 70%;
	display: inline-block;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.LargeFont {
	font-size: 20px;
}
.NormalFont {
	font-size: 16px;
}
.SmallFont {
	font-size: 14px;
}




