@value colors: "../../utils/colors.css";
@value primary, primaryTwo from colors;

.Navigation {
	width: 100%;
	height: 50px;
	padding: 0 10px; 		/* This should change on larger screens */
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-webkit-box-shadow: 0px 2px 4px rgba(0,0,0,.15);
	        box-shadow: 0px 2px 4px rgba(0,0,0,.15);
	position: relative;
	z-index: 2000;
	background: -webkit-gradient(linear, left top, right top, from(primaryTwo), to(primary));
	background: -webkit-linear-gradient(left, primaryTwo, primary);
	background: -o-linear-gradient(left, primaryTwo, primary);
	background: linear-gradient(90deg, primaryTwo, primary);
	background: #000;
}

.FullNavBar {
	/* background: linear-gradient(90deg, #FC3565, #FF5757); */
	-ms-flex-pack: justify;
	    justify-content: space-between;
	height: 70px;
}

.Menu {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: end;
	    justify-content: flex-end;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-positive: 1;
	    flex-grow: 1;
}
.Search {
	margin: auto 20px;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: end;
	    justify-content: flex-end;
}


.Mobile .Menu,
.Mobile .Search {
	display: none;
}


