.Avatar {
	border-radius: 100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.AvatarImage {
	width: 50px;
	height: 50px;
	border-radius: 100%;
	background-color: #ddd;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	position: relative;
	cursor: pointer;
	overflow: hidden;
}
.AvatarImage > img {
	width: 50px;
	height: 50px;
}




