@value colors: "../../../utils/colors.css";
@value primary, secondary from colors;

.Header {
	width: 80%;
	max-width: 300px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	position: relative;
	margin: 30px auto;
	margin-bottom: 50px;
}
.HeaderAlbums {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}
.HeaderAlbum {
	width: 40px;
	height: 40px;
	border: 3px solid #000;
}
.HeaderAlbum > img {
	width: 40px;
	height: 40px;
	
	-webkit-box-shadow: 0 4px 4px rgba(0,0,0,.25);
	
	        box-shadow: 0 4px 4px rgba(0,0,0,.25);
}
.HeaderYear {
	text-align: center;
}
.Connector {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	width: calc(100% - 86px);
	text-align: center;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	height: 1px;
	background: -webkit-gradient(linear, left top, left bottom, from(primary), to(secondary));
	background: -webkit-linear-gradient(primary, secondary);
	background: -o-linear-gradient(primary, secondary);
	background: linear-gradient(primary, secondary);
}

.RatingContainer {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	background: #FFF;

}




